import axios from "axios";
import { VUE_APP_API_HOST } from "src/constants";

export class TranslateService {
  static async translate({ text, to }) {
    const response = await axios.get(`${VUE_APP_API_HOST}/api/translate`, {
      params: {
        from: 'auto',
        text,
        to,
      },
    });

    return response.data.translation;
  }
}
