import { PostService } from '@/services'

const service = new PostService()

export default {
  namespaced: true,

  state: () => {
    return {
      posts: [],
      newPostsCount: 0,
      post: null,
    }
  },

  getters: {
    items: (state) => state.posts,
    item: (state) => state.post,
    newPostsCount: (state) => state.newPostsCount,
  },
  mutations: {
    setPost (state, post) {
      state.post = post
    },
    updateNewPostsCount (state, count) {
      state.newPostsCount = count
    },
    addPosts (state, posts) {
      if (state.posts.length === 0) {
        state.posts = posts

        return
      }

      // Add new posts to the existing posts and delete post clones
      const currentPostsCount = state.posts.length

      const newPosts = posts.filter((post) => {
        return !state.posts
          .slice(currentPostsCount - 10, currentPostsCount)
          .some((p) => p.id === post.id)
      })

      state.posts = [...state.posts, ...newPosts]
    },
    reset (state) {
      state.posts = []
    },
  },
  actions: {
    async loadPost ({ commit }, id) {
      try {
        const post = await service.getPostById(id)

        commit('setPost', post)
      } catch (error) {
        console.log(error.message)
      }
    },
    async loadPosts ({ commit }) {
      try {
        commit('reset')

        const posts = await service.getPosts()

        commit('addPosts', posts)
      } catch (error) {
        console.log(error.message)
      }
    },

    async loadMorePosts ({ commit }) {
      try {
        const posts = await service.loadMorePosts()

        commit('addPosts', posts)
      } catch (error) {
        console.log(error.message)
      }
    },
  },
}
