// router.beforeEach(initMiddlewares(store));
export default route(() => {
  const Router = createRouter({
    history: createWebHistory("/"),
    scrollBehavior(to, from) {
      const res = {};

      if (from.params.id) {
        res["el"] = `#post-${from.params.id}`;
      } else {
        res["top"] = 0;
      }

      return res;
    },
    routes,
  });

  return Router;
});
