import axios from 'axios';
import { ChannelService } from './channel.service';
import { VUE_APP_API_HOST } from '../constants';

const limit = 10;
let page = 1;
let totalPages = 0;

const getChannels = () =>
  Object.keys(ChannelService.getStorageChannels()).filter((key) => {
    if (ChannelService.getStorageChannels()[key].active) {
      return localStorage.getItem('category') !== 'all'
        ? ChannelService.getStorageChannels()[key].category ===
            localStorage.getItem('category')
        : true;
    }

    return false;
  });

const fetchPosts = async (page) =>
  axios.get(`${VUE_APP_API_HOST}/api/posts`, {
    params: {
      channels: getChannels(),
      // timestamps: Date.now(),
      pageSize: limit,
      page,
    },
  });

const getPosts = async () => {
  page = 1;
  const postsData = await fetchPosts(page);
  totalPages = postsData.data;

  return postsData.data.rows;
};

const getPostById = async (id) => {
  try {
    const post = await axios.get(`${VUE_APP_API_HOST}/api/posts/${id}`);

    return {
      ...post.data._doc,
      childrens: post.data.childrens,
    };
  } catch (err) {
    console.error(err);
    return null;
  }
};

const loadMorePosts = async () => {
  if (page >= totalPages) return [];
  page += 1;
  const postsData = await fetchPosts(page);

  return postsData.data.rows;
};

export class PostService {
  constructor() {
    this.getChannels = getChannels;
    this.fetchPosts = fetchPosts;
    this.getPosts = getPosts;
    this.getPostById = getPostById;
    this.loadMorePosts = loadMorePosts;
  }
}
