import { ref, onMounted, onUnmounted } from 'vue';
import { RequestPreloadQueue } from './request-preload-queue.service';
import { sleep } from '@/utils';

export const PreloaderService = () => {
  const loading = ref(true);
  const showScrollBtn = ref(false);
  const inited = ref(false);

  const showHeaderOnScroll = async () => {
    await sleep(500);
    const header = document.querySelector('.app-header');

    const makeFixed = (st, el, offset = 0, id) => {
      try {
        if (!el) {
          return;
        }

        const elHeight = el.offsetHeight;

        if (st > lastScrollTop) {
          el.style.top = `-${elHeight}px`;
        } else {
          el.style.top = offset + 'px';
        }

        if (st > 0) {
          el.style.position = 'fixed';
          el.style.backgroundColor = 'rgba(0, 0, 0, 0.05)';
          el.style.backdropFilter = 'blur(10px)';
        } else {
          el.style.position = 'relative';
          el.style.backgroundColor = 'transparent';
          el.style.backdropFilter = 'blur(10px)';
          el.style.top = '0px';
        }
      } catch (error) {
        console.log(id, error);
      }
    };

    let lastScrollTop = 0;

    window.addEventListener('scroll', () => {
      const st = window.pageYOffset || document.documentElement.scrollTop;

      makeFixed(st, header, 0, 'a');

      lastScrollTop = st <= 0 ? 0 : st;
    });
  };

  const handleScroll = () => {
    if (window.scrollY > 1200) {
      showScrollBtn.value = true;
    } else {
      showScrollBtn.value = false;
    }
  };

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const init = async () => {
    await sleep(1000);
    await RequestPreloadQueue.callRequests();

    document.body.style.overflow = 'auto';
    loading.value = false;
  };

  onMounted(async () => {
    window.addEventListener('scroll', handleScroll);

    if (window.screen.width < 768) {
      await showHeaderOnScroll();
    }

    if (inited.value) {
      return;
    }

    await init();

    inited.value = true;
  });

  onUnmounted(() => {
    window.removeEventListener('scroll', handleScroll);
  });

  return {
    loading,
    showScrollBtn,
    scrollToTop,
    init,
  };
};
